@charset "UTF-8";
* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  min-width: 1440px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  color: #333333;
  counter-reset: section-counter; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden; }

.container {
  width: 1380px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px; }

.page {
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header" "content" "footer"; }

.header {
  grid-area: header;
  background-color: #f2f8fb; }

.page__content {
  grid-area: content; }

.footer {
  grid-area: footer;
  background-color: #f2f8fb; }

.page__link {
  text-decoration: none;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #06a9b3; }
  .page__link:hover {
    text-decoration: underline; }
  .page__link:focus {
    text-decoration: underline;
    outline: none; }

.button {
  padding: 16px 30px;
  display: inline-block;
  font-family: inherit;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  background-color: #06a9b3;
  text-decoration: none;
  border: none; }
  .button:hover {
    padding: 15px 29px;
    background: #ffffff;
    color: #06a9b3;
    border: 1px solid #06a9b3; }
  .button:focus {
    padding: 15px 29px;
    background: #ffffff;
    color: #06a9b3;
    border: 1px solid #06a9b3;
    outline: none; }

.page__content h1 {
  margin: 0;
  font-size: 44px;
  line-height: 62px;
  color: #333333; }

.title {
  position: relative;
  margin: 0;
  padding: 0 0 0 115px;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #06a9a3; }

.title::before {
  position: absolute;
  left: 0;
  top: 0;
  counter-increment: section-counter;
  /* Инкрементирует счетчик*/
  content: "0" counter(section-counter);
  /* Отображает текущее значение счетчика */
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  color: #06a9b3; }

.title::after {
  content: "";
  position: absolute;
  left: 25px;
  top: 9px;
  width: 81px;
  height: 1px;
  border-top: 1px solid #06a9b3; }

.headline {
  margin: 60px 0 25px 0;
  font-size: 32px;
  line-height: 48px; }

.header__nav {
  display: grid;
  grid-template-columns: auto 1fr auto auto;
  grid-column-gap: 65px; }

.header__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  list-style: none; }

.header__item {
  padding-right: 65px; }

.header__item:last-child {
  padding-right: 0; }

.header__container--logo {
  padding: 20px 110px 20px 0;
  text-align: center; }

.header__container--search {
  padding: 25px 0 23px; }

.header__search {
  width: 16px;
  height: 16px;
  padding: 7px 0;
  display: block;
  background-image: url("/img/search_icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px 16px; }

.header__user {
  padding: 25px 0 23px; }

.welcome {
  padding-bottom: 100px;
  background-color: #f2f8fb; }

.welcome__content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(5, auto);
  grid-column-gap: 24px; }

.welcome__city {
  margin: 80px 0 10px 0;
  grid-area: 1 / 2 / 2 / 8;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #aaaaaa; }

.welcome h1 {
  grid-area: 2 / 2 / 3 / 8;
  z-index: 1; }

.welcome__text {
  margin: 10px 0 40px 0;
  grid-area: 3 / 2 / 4 / 8;
  z-index: 1;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: #333333; }

.welcome__button {
  margin-bottom: 90px;
  grid-area: 4 / 2 / 5 / 4; }

.welcome__pic {
  grid-area: 1 / 6 / -1 / -1; }

.welcome__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover; }

.welcome__list {
  padding: 0;
  margin: 10px 0 0 0;
  grid-area: 5 / 2 / 6 / 5;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.welcome__item {
  margin-right: 73px; }

.welcome__item:last-child {
  margin-right: 0; }

.welcome__link {
  width: 44px;
  height: 44px;
  display: block; }

.welcome__link--facebook {
  background-image: url("/img/fb_icon.svg");
  background-repeat: no-repeat;
  background-position: center; }
  .welcome__link--facebook:hover {
    background-image: url("/img/fb-hover_icon.svg"); }
  .welcome__link--facebook:focus {
    background-image: url("/img/fb-hover_icon.svg");
    outline: none; }

.welcome__link--instagram {
  background-image: url("/img/ins_icon.svg");
  background-repeat: no-repeat;
  background-position: center; }
  .welcome__link--instagram:hover {
    background-image: url("/img/ins-hover_icon.svg"); }
  .welcome__link--instagram:focus {
    background-image: url("/img/ins-hover_icon.svg");
    outline: none; }

.welcome__link--vk {
  background-image: url("/img/vk_icon.svg");
  background-repeat: no-repeat;
  background-position: center; }
  .welcome__link--vk:hover {
    background-image: url("/img/vk-hover_icon.svg"); }
  .welcome__link--vk:focus {
    background-image: url("/img/vk-hover_icon.svg");
    outline: none; }

.welcome__controls {
  grid-area: 5 / 6 / 6 / -1;
  display: grid;
  grid-template-columns: auto auto 1fr;
  z-index: 1; }

.welcome__btn {
  width: 105px;
  height: 66px;
  padding: 0;
  border: none;
  background-color: #ffffff;
  cursor: pointer; }

.welcome__btn--left {
  background-image: url("/img/arw_left_icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30px 16px; }
  .welcome__btn--left:hover {
    background-image: url("/img/arw_left-hover_icon.svg"); }
  .welcome__btn--left:focus {
    background-image: url("/img/arw_left-hover_icon.svg");
    outline: none; }

.welcome__btn--right {
  background-image: url("/img/arw_right_icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30px 16px; }
  .welcome__btn--right:hover {
    background-image: url("/img/arw_right-hover_icon.svg"); }
  .welcome__btn--right:focus {
    background-image: url("/img/arw_right-hover_icon.svg");
    outline: none; }

.welcome__page {
  margin: 10px 30px 0 0;
  text-align: right;
  font-weight: normal;
  font-size: 32px;
  line-height: 48px;
  color: #ffffff; }

.promo {
  position: relative;
  margin-top: 100px;
  margin-bottom: 95px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 24px; }

.promo::before {
  content: "01";
  position: absolute;
  left: 145px;
  top: 0;
  font-weight: 900;
  font-size: 65px;
  line-height: 65px;
  color: #eaf2f6; }

.promo::after {
  content: "Место";
  position: absolute;
  left: 145px;
  top: 60px;
  font-weight: 900;
  font-size: 65px;
  line-height: 65px;
  color: #eaf2f6;
  writing-mode: vertical-lr;
  transform: scale(-1, -1) translateY(-40px); }

.promo__header {
  grid-column: 5 / 9; }

.promo__content {
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 24px; }

.promo__content > * {
  grid-column: 5 / 9; }

.promo__text {
  margin-top: 0;
  margin-bottom: 25px; }

.promo__img {
  margin: 0 0 25px 0; }

.promo__img--big {
  grid-column: 2 / -2; }

.promo__blockquote {
  margin: 0 0 35px 0;
  padding-left: 30px;
  grid-column: 4 / 10;
  font-weight: normal;
  font-size: 22px;
  line-height: 40px;
  border-left: 1px solid #06a9b3; }

.promo__btn {
  margin-top: 10px;
  grid-column: 5 / 8; }

.promo__text--right {
  grid-column: -4 / -2;
  font-size: 14px;
  line-height: 24px; }

.promo__text--left {
  margin-top: 20px;
  grid-column: 2 / 4;
  font-size: 14px;
  line-height: 24px; }

.catalog {
  padding: 0 0 140px 0;
  background-color: #f2f8fb; }

.catalog__content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 24px; }

.catalog__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 100px;
  padding-bottom: 13px; }

.catalog__item {
  grid-column: span 3;
  display: grid;
  grid-template-columns: 3fr;
  grid-template-rows: 220px auto;
  grid-gap: 17px 24px;
  background-color: #ffffff;
  border: 1px solid #c4c4c4; }

.catalog__item--horizontal {
  grid-column: 3 / -3;
  grid-template-columns: 3fr 5fr;
  grid-template-rows: auto; }

.catalog__text {
  padding: 0 18px 23px; }

.catalog__text--horizontal {
  padding: 20px 24px 24px 0; }

.catalog__pic {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.catalog__title {
  margin: 0 0 5px 0;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  color: #333333; }

.catalog__para {
  margin: 0 0 17px 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #aaaaaa; }

.catalog__text dl {
  margin: 0;
  padding: 15px 0 0 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 50px;
  border-top: 2px solid #eaf2f6; }

.catalog__text--horizontal dl {
  grid-column-gap: 175px; }

.catalog__text dt {
  margin: 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px; }

.catalog__text dd {
  margin: 0;
  grid-row: 1 / 2;
  font-weight: 300;
  font-size: 32px;
  line-height: 36px; }

.feedback {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 20px; }

.feedback__header {
  margin-top: 100px;
  grid-column: 2 / span 4; }

.feedback__headline {
  margin: 60px 0 0 0; }

.feedback__img {
  width: 100%;
  height: 100%;
  grid-column: 7 / -1;
  grid-row: 1 / 3;
  object-fit: cover; }

.feedback__form {
  margin-bottom: 90px;
  grid-column: 2 / span 4;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-column-gap: 24px;
  grid-row-gap: 6px;
  grid-template-areas: "title title name name surname surname" "email email email email email email" "phone phone phone phone phone phone" "agreement agreement agreement subscribe subscribe subscribe" "submit submit submit . . ."; }

.feedback__tit {
  grid-area: title; }

.feedback__name {
  grid-area: name; }

.feedback__surname {
  grid-area: surname; }

.feedback__email {
  grid-area: email; }

.feedback__phone {
  grid-area: phone; }

.feedback__checkbox--agreement {
  margin-top: 25px;
  grid-area: agreement; }

.feedback__checkbox--subscribe {
  margin-top: 15px;
  grid-area: subscribe; }

.feedback__btn {
  grid-area: submit; }

.feedback__label {
  font-weight: 300;
  font-size: 12px;
  line-height: 18px;
  color: #333333; }

.feedback__field {
  width: 100%;
  padding: 12px 16px;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  color: #333333;
  border: 1px solid #cfdce3;
  box-sizing: border-box; }
  .feedback__field::placeholder {
    font-weight: 300;
    font-size: 16px;
    line-height: 20px;
    color: rgba(51, 51, 51, 0.3); }
  .feedback__field:hover {
    border: 1px solid #06a9b3; }
  .feedback__field:focus {
    border: 1px solid #06a9b3;
    outline: none; }

.feedback__label--checkbox {
  position: relative;
  max-width: 180px;
  padding-left: 25px;
  display: inline-block;
  font-weight: 300;
  font-size: 12px;
  line-height: 16px; }
  .feedback__label--checkbox::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 18px;
    height: 18px;
    border: 1px solid #cfdce3; }

.feedback__input--checkbox:checked + .feedback__label--checkbox::after {
  content: "";
  position: absolute;
  left: 4px;
  top: 4px;
  width: 12px;
  height: 12px;
  background-color: #06a9b3; }

.footer__wrap {
  display: grid;
  grid-template-columns: auto 1fr auto; }

.footer__logo {
  margin: 30px 0; }

.footer__list {
  margin: 0;
  padding: 0 0 0 135px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  list-style: none; }

.footer__item {
  padding-left: 65px; }
  .footer__item :last-child {
    padding-right: 0; }

.footer__copyright {
  margin-top: 30px; }
